.condition{
  padding: 3.125em 0 4.125em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 54.75em) 50.25em;
    gap: 4.5em;
    position: relative;
  }
  &__col{
    position: relative;
  }
  &__arrow{
    position: absolute;
    width: 4.91em;
    top: -6.8em;
    right: 20.93em;
    transform: rotate(142.847deg);
  }
  &__card{
    position: absolute;
    width: 6em;
    bottom: 6.375em;
    right: -3em;
    transform: rotate(12.348deg);
  }
  &__items{
    margin-top: 2.625em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__img{
    position: absolute;
    top: -2.25em;
    right: -6.1em;
    width: 30.2em;
  }
}

.condition-item{
  border-radius: 0.5em;
  background: rgba(35, 35, 35, 0.01);
  box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
  padding: 1.875em 1.875em 2.19em;
  color: #0F4A44;
  &__title{
    font-weight: 700;
  }
  &__title,
  &__desc{
    font-size: 1.125em;
    line-height: 150%;
  }
  &__desc{
    margin-top: .78em;
  }
}

.main{
  padding-bottom: 7.5em;
  &__title{
    position: relative;
    img{
      position: absolute;
      width: 15.0625em;
      right: -.3125em;
      top: 100%;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 53.25em) 27.25em 23.5em;
    gap: 1.25em;
    margin-top: 4em;
  }
}

.main-form{
  min-height: 100%;
  padding: 1.875em 3.125em 1.375em;
  border-radius: 0.5em;
  background: #F6F6F6;
  box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
  &__title{
    font-size: 1.5em;
    line-height: 125%;
    font-weight: 700;
    text-align: center;
    color: $dark;
  }
  &__fields{
    margin-top: .84em;
    @include grid100gap(.625em);
  }
  &__btn{
    margin: 1.25em 0 1em;
    width: 100%;
    span{
      font-size: 1.125em;
    }
  }
  &__hint{
    font-size: .75em;
    line-height: 125%;
    opacity: .5;
    text-align: center;
  }
  .filter-slider{
    margin-top: 1em;
  }
}


@media screen and (max-width: 992px){
  .condition{
    padding: 1.25em 0;
    &__grid{
      grid-template-columns: 100%;
    }
    &__card,
    &__arrow,
    &__col{
      display: none;
    }
    &__items{
      margin-top: 1.25em;
      gap: .875em;
      grid-template-columns: repeat(2,calc(50% - .4375em));
    }
  }

  .condition-item{
    padding: 1.25em;
    min-height: 8em;
    &__desc{
      margin-top: .625em;
      font-size: 1em;
    }
  }

  .main{
    padding-bottom: 1.25em;
    &__title{
      img{
        width: 7.8125em;
        right: 0;
        margin-top: .125em;
      }
    }
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
      margin-top: 2em;
    }
    &__col{
      display: none;
    }
  }

  .main-form{
    padding: 1.5em .81em;
  }
}


@media screen and (max-width: 576px){
  .condition{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .main{
    &__title{
      img{
        right: 50%;
        transform: translateX(50%);
      }
    }
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      order: 2;
    }
  }
}
