@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.375em;
    border-radius: .5em;
    height: 3.375em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-blue{
    background-color: $primary;
    color: $white;
    box-shadow: 2px 1px 4px 0 rgba(255, 255, 255, 0.35) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25);
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-white-outline{
    border: 1px solid $white;
    color: $white;
    &:hover{
        background-color: $white;
        color: $primary;
    }
}
