.header{
  padding: 3em 0 3.625em;
  &__logo{
    width: 9.0625em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    width: 15.875em;
  }
}

.nav{
  display: flex;
  &__link{
    margin-right: 1.19em;
    &.m-btn-blue{
      min-width: 11.75em;
    }
    &:last-child{
      margin: 0;
    }
  }
}

.nav-item{
  margin-right: 1.19em;
  &:last-child{
    margin: 0;
  }
  position: relative;
  &__show{
    cursor: pointer;
  }
  &__list{
    a{
      font-size: 1em;
      line-height: 143%;
      padding: .5em;
      border-radius: .25em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      &:hover{
        color: $primary;
      }
    }
    li{
      &:last-child{
        border-bottom: none;
      }
    }
  }
  @media screen and (min-width: 993px){
    &:hover{
      .nav-item__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__hidden{
      color: $black;
      position: absolute;
      top: 100%;
      left: -1.5em;
      padding-top: .75em;
      margin-top: -1px;
      width: auto;
      white-space: nowrap;
      z-index: 4;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      display: block !important;
    }
    &__inner{
      padding: 1em;
      background-color: $white;
      box-shadow: 0 6px 16px 0 rgba(20,20,20,.1);
    }
  }
}

.menu__hint,
.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: .875em 0 2em;
    &__logo{
      position: relative;
      z-index: 11;
      width: 4.6875em;
    }
    &__phone{
      padding: 0 1.1875em;
      height: 2.25em;
      width: auto;
      position: relative;
      z-index: 11;
      span{
        font-size: .875em;
      }
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    background-color: $white;
    height: $vh;
    width: 100%;
    padding: 4.875em 0 0;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__scroll{
      height: 100%;
      overflow-y: auto;
      padding: 0 1.5em 1.5em;
    }
    &__hint{
      display: block;
      font-size: 1em;
      line-height: 119%;
      font-weight: 700;
      max-width: 15em;
      margin-top: 2.5em;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      min-width: 11.5em !important;
      margin: 0 0 1.25em;
    }
  }

  .nav-item{
    text-align: center;
    margin: 0 0 1.25em;
    &__show{
      display: flex;
      &:after{
        display: none;
      }
    }
    &__list{
      li{
        margin-top: 1.5em;
      }
      a{
        padding: 0;
        font-size: 1.125em;
        line-height: 95%;
        letter-spacing: -.02em;
        font-family: $font2;
        font-weight: 500;
      }
    }
  }

  .burger{
    z-index: 11;
    width: 1.875em;
    height: 1.875em;
    display: block;
    position: relative;
    flex-shrink: 0;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: all .3s;
    }
    ._view2{
      opacity: 0;
      width: 1.5em;
    }
  }
}


