.faq{
  padding: 3.125em 0 0;
  &__items{
    width: 63.75em;
    max-width: 100%;
    margin: 2.5em auto 0;
    @include grid100gap(1.875em);
  }
}

.partners{
  padding: 3.125em 0;
  &__top{
    @include grid100gap(1.25em);
    text-align: center;
    width: 33.875em;
    max-width: 100%;
    margin: 0 auto;
  }
  &__items{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
  &__item{
    border-radius: 0.5em;
    background: rgba(35, 35, 35, 0.01);
    box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
    height: 8.5em;
    @include center;
    padding: .125em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
}

.slider{
  &__wrap{
    overflow: hidden;
  }
  .swiper-container{
    width: calc(100% + 1.25em);
  }
  .swiper-wrapper{
    align-items: stretch;
  }
  .swiper-slide{
    padding-right: 1.25em;
    height: auto;
  }
}

.reviews{
  padding: 3.125em 0;
  overflow: hidden;
}

.reviews-slider{
  margin-top: 2.625em;
  .swiper-slide{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    width: calc(100% / 3);
    &-active,
    &-next,
    &-next+.swiper-slide{
      opacity: 1;
      pointer-events: unset;
    }
  }
  .swiper-nav{
    margin-top: 1.5em;
    display: flex;
    justify-content: center;
  }
  .swiper-button{
    margin: 0 .75em;
    position: static;
  }
}

.review-card{
  padding: 1em 1em 1.75em;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  border-radius: 0.75em;
  background: #F6F6F6;
  box-shadow: 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05), 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset;
  color: #1E554F;
  .rating{
    justify-content: flex-end;
  }
  &__content{
    margin-top: 1em;
    padding: 0 3em 1.5em 1.125em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 1.4375em;
      height: 1.125em;
      @include bgImgSize(100%)
    }
    &:before{
      background-image: url(../img/quote.svg);
      top: -2em;
      left: 0;
    }
    &:after{
      background-image: url(../img/quote2.svg);
      right: 0;
      bottom: 0;
    }
  }
  &__bottom{
    margin-top: auto;
    padding-top: 1.69em;
  }
}

.swiper-button{
  width: 4.25em;
  height: 4.25em;
  border-radius: 0.75em;
  img{
    width: 2em;
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding-top: 1.25em;
    &__items{
      margin-top: 1.25em;
      gap: .625em;
    }
  }

  .partners{
    padding: 1.25em 0;
    &__top{
      gap: .625em;
    }
    &__items{
      margin-top: 1.75em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
    &__item{
      height: 5.875em;
      padding: .75em;
    }
  }

  .reviews{
    padding: 1.25em 0;
  }

  .reviews-slider{
    margin-top: 1.25em;
    .swiper-slide{
      width: 50%;
    }
    .swiper-nav{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .partners{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .reviews-slider{
    .swiper-slide{
      width: 100%;
    }
  }
}
