.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: .9375em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $primary;
    }
  }
  .nice-select,
  input,
  textarea{
    border: none;
    padding: 0 1.125em;
    height: 3em;
    width: 100%;
    font-size: 1.125em;
    line-height: 150%;
    border-radius: 0.67em;
    background: #F8F8F8;
    box-shadow: 7px 6px 11.7px 0 rgba(0, 0, 0, 0.15), 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset;
    color: rgba(0, 5, 5, 1);
    &::placeholder {
      color: rgba(0, 5, 5, 0.30);
    }
    &:focus {
      border-color: #9CA0A0;
      box-shadow: 0 .25em .4375em 0 rgba(47, 63, 67, 0.03), 0 .25em .3125em 0 rgba(0, 0, 0, 0.02) inset;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 13.0625em;
    resize: vertical;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.6875em;
    padding: 0 0 0 1.25em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:focus{
      border: 1px solid $black;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -1px;
      border-radius: 0 0 .1875em .1875em;
      width: calc(100% + 2px);
      left: -1px;
      border: 1px solid $black;
      background: #FFF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.6875em;
      font-size: 1em;
      line-height: 157%;
      padding: .5em 1.25em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      border-bottom: 1px solid $black;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.pass-toggle{
  position: absolute;
  bottom: 1em;
  right: 1.25em;
  width: 1.25em;
  height: 1.6875em;
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
  &._active{
    g{
      opacity: 1;
    }
  }
}

.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  label{
    position: relative;
    cursor: pointer;
    display: block;
    padding-left: 2.125em;
    min-height: 1.25em;
    padding-top: .0625em;
    span{
      font-size: .8125em;
      line-height: 140%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      border: 1px solid #DADEDF;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
  &._big{
    label{
      padding-top: 0;
      span{
        font-size: 1em;
        line-height: 140%;
      }
    }
  }
  &._gray{
    label{
      color: #6B6E72;
    }
  }
  &._error{
    input:not(:checked){
      &~label:before{
        border-color: #FF1515;
      }
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check2.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}


.rating{
  display: flex;
}

.rating-input {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  position: relative;
  &--read-only {
    pointer-events: none;
  }
  &__field {
    position: absolute;
    left: 0;
    top: 100%;
    opacity: 0;
    pointer-events: none;
  }
  &__item {
    flex-shrink: 0;
    transition: all .3s;
    width: 1.25em;
    margin-right: .125em;
    svg {
      width: 100%;
      height: auto;
    }
    path {
      fill: $gray;
      stroke: $gray;
    }
    &:hover {
      cursor: pointer;
      path {
        fill: #F1A44A;
      }
      &~.rating-input__item {
        path {
          fill: #F1A44A;
        }
      }
    }
  }
}

.rating-input[data-total-rating="1"]:not(:hover) .rating-input__item:nth-child(n+5),
.rating-input[data-total-rating="2"]:not(:hover) .rating-input__item:nth-child(n+4),
.rating-input[data-total-rating="3"]:not(:hover) .rating-input__item:nth-child(n+3),
.rating-input[data-total-rating="4"]:not(:hover) .rating-input__item:nth-child(n+2),
.rating-input[data-total-rating="5"]:not(:hover) .rating-input__item:nth-child(n+1) {
  path {
    fill: #F1A44A;
    stroke: #F1A44A;
  }
}


