.footer{
  padding: 2.5em 0;
  background: #1A0600;
  box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
  color: $white;
  &__top{
    display: grid;
    grid-template-columns: 24.75em calc(100% - 24.75em - 28.875em) 15.875em;
    gap: 6.5em;
    align-items: flex-start;
    &-col3{
      @include grid100gap(1.25em);
    }
  }
  &__logo{
    display: block;
    width: 9.0625em;
    margin-bottom: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    font-size: .75em;
    line-height: 125%;
    color: rgba(250, 251, 251, 0.50);
  }
  &__btn{
    width: 100%;
  }
  &__desc{
    font-size: 1em;
    line-height: 119%;
    font-weight: 700;
    text-align: right;
  }
  &__bottom{
    margin-top: 1.875em;
  }
  &__links{
    gap: 1.25em 2.625em;
  }
  &__link,
  &__c{
    font-size: .875em;
    line-height: 120%;
  }
}

.footer-nav{
  margin-bottom: -1.25em;
  ul{
    column-count: 2;
    column-gap: .75em;
  }
  li{
    margin-bottom: 1.25em;
    display: inline-block;
    width: 100%;
  }
  a{
    font-size: 1.125em;
    line-height: 150%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.5em 0 1.875em;
    &__top{
      grid-template-columns: calc(100% - 1.875em - 15.875em) 15.875em;
      gap: 1.875em;
      position: relative;
      padding-top: 2.75em;
      &-col{
        order: 2;
      }
      &-col3{
        grid-column: 2/3;
        grid-row: 1/3;
      }
    }
    &__logo{
      width: 4.6875em;
      margin: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
    &__bottom{
      display: block;
    }
    &__links{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      grid-template-columns: 100%;
      &-col3{
        grid-column: unset;
        grid-row: unset;
      }
    }
    &__btn{
      width: 15.875em;
    }
    &__desc{
      text-align: left;
    }
    &__bottom{
      margin-top: 2.5em;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
    }
  }
}
