.main{
  &__items{
    @include grid100gap(1.25em);
  }
  &__col{
    position: relative;
  }
  &__img{
    position: absolute;
    top: -3em;
    right: -5.25em;
    width: 31.875em;
    z-index: -1;
  }
}

.main-item{
  padding: .125em;
  background: linear-gradient(90deg, #F6F6F6 0%, #4591F0 100%);
  border-radius: .5em;
  &:nth-child(even){
    background: linear-gradient(90deg, #4591F0 0%, #F6F6F6 100%);
  }
  &__inner{
    padding: 1em 1.5em;
    display: flex;
    align-items: center;
    background-color: $white;
    border-radius: .5em;
    min-height: 5.75em;
  }
  &__img{
    width: 3.75em;
    height: 3.1875em;
    @include center;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.125em;
    line-height: 150%;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .main{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: .5em 1.25em;
    }
  }

  .main-item{
    &:nth-child(3){
      background: linear-gradient(90deg, #4591F0 0%, #F6F6F6 100%);
    }
    &:nth-child(4){
      background: linear-gradient(90deg, #F6F6F6 0%, #4591F0 100%);
    }
    &__inner{
      padding: .375em .375em .625em;
      display: block;
      min-height: 5.125em;
      height: 100%;
    }
    &__img{
      height: 2.125em;
      width: 3.125em;
      justify-content: unset;
      margin: 0 0 .625em;
    }
    &__title{
      font-size: .875em;
      span{
        white-space: nowrap;
        font-size: .71em;
        font-weight: 400;
      }
      &._lh110{
        line-height: 110%;
      }
    }
  }
}
