.how-works{
  padding: 3.125em 0;
  &__inner{
    position: relative;
  }
  &__arrow{
    top: -3.75em;
    right: 9.8em;
    width: 4.93em;
    transform: rotate(-135.67deg);
    position: absolute;
  }
  &__title{
    position: relative;
    img{
      position: absolute;
      top: 100%;
      right: 0;
      width: 10.8125em;
    }
  }
  &__grid{
    margin-top: 3.125em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
    position: relative;
    &:before{
      content: '';
      width: 100%;
      position: absolute;
      max-width: 56.25em;
      height: .125em;
      top: 2.625em;
      left: 50%;
      transform: translate(-50%,-50%);
      background-image: url(../img/line2.svg);
      background-size: auto 100%;
      background-repeat: no-repeat;
      background-position: center;
      z-index: -1;
    }
  }
}

.step-item{
  &__num{
    width: 5.25em;
    height: 5.25em;
    background-color: $primary;
    color: $white;
    @include center;
    font-weight: 700;
    border-radius: 50%;
    margin: 0 auto 1.25em;
    box-shadow: 3px 4px 4px 0 rgba(255, 255, 255, 0.30) inset;
    filter: drop-shadow(0 7px 13px rgba(69, 69, 69, 0.05)) drop-shadow(3px 2px 3.5px rgba(0, 0, 0, 0.15));
    backdrop-filter: blur(24.5px);
  }
  &__middle{
    padding: 2em 1em;
    border-radius: 0.5em;
    background: rgba(35, 35, 35, 0.01);
    box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
    backdrop-filter: blur(24.5px);
    text-align: center;
  }
  &__title{
    font-size: 1.125em;
    line-height: 130%;
    font-weight: 700;
    font-family: $font2;
  }
  &__desc{
    font-size: .875em;
    line-height: 120%;
    margin-top: 1em;
    color: #0F4A44;
  }
  &__link{
    width: 100%;
    margin-top: .625em;
  }
}

.why{
  padding: 3.125em 0 2em;
  &__items{
    margin-top: 2.625em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
}

.why-item{
  display: flex;
  align-items: center;
  border-radius: 0.5em;
  background: $primary;
  color: $white;
  box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
  padding-right: 1.75em;
  &__icon{
    height: 7.375em;
    width: 7.375em;
    @include center;
    margin-right: .625em;
    &._padding{
      padding: .5em;
    }
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.125em;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .how-works{
    padding: 1.25em 0;
    &__arrow{
      display: none;
    }
    &__title{
      img{
        width: 7.8125em;
      }
    }
    &__grid{
      margin-top: 2em;
      grid-template-columns: 100%;
      &:before{
        width: 10.625em;
        top: 1.75em;
      }
    }
  }

  .step-item{
    &:not(:first-child){
      .step-item__num{
        display: none;
      }
    }
    &__num{
      width: 3.5em;
      height: 3.5em;
      span{
        font-size: .67em;
      }
    }
    &__middle{
      padding: 1.25em;
    }
  }

  .why{
    padding: 1.25em 0;
    &__items{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
  }

  .why-item{
    &__icon{
      height: 7.375em;
      width: 6.75em;
      margin-right: .44em;
    }
  }
}


