@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
@import "components/filter-slider";
._flex,
._flex-between,
._flex-center{
  display: flex;
  align-items: center;
  &._start{
    align-items: unset;
  }
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

._mt48{
  margin-top: 3em;
}

._mt32{
  margin-top: 2em;
}

._mt24{
  margin-top: 1.5em;
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.05);
    }
  }
  img{
    transition: all .6s;
  }
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.12);
    pointer-events: none;
    border-radius: inherit;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2.5em;
    height: 2.5em;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.10);
    background: #1A1A1A;
    @include lastM(0 .75em 0 0);
    @include center;
    &:hover{
      path{
        fill-opacity: 1;
        &:not(._not-fill){
          fill: #fff;
        }
      }
    }
  }
}

.qa-card{
  border-radius: 0.75em;
  background: rgba(35, 35, 35, 0.01);
  box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
  &._active{
    background: linear-gradient(180deg, rgba(69, 145, 240, 0.15) 0%, rgba(69, 145, 240, 0.02) 92.31%);
    .qa-card__btn {
      background-color: $primary;
      svg{
        transform: rotate(-45deg);
      }
      path{
        fill: $white;
      }
    }
  }
  &__show{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.69em 2em;
  }
  &__title{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 700;
    color: $primary;
  }
  &__btn{
    margin-left: 1em;
    width: 3.125em;
    height: 3.125em;
    @include center;
    flex-shrink: 0;
    border-radius: 0.75em;
    svg{
      transition: all .3s;
      width: 1.875em;
    }
  }
  &__hidden{
    margin-top: -1.69em;
    padding: 1em 2em 3em;
    display: none;
  }
  &__text{
    font-size: 1.25em;
    line-height: 120%;
  }
}


@media screen and (max-width: 992px){
  .qa-card{
    &__show{
      padding: .75em 1.125em;
    }
    &__title{
      font-size: 1em;
      max-width: 80%;
    }
    &__btn{
      width: 2em;
      height: 2em;
      svg{
        width: .9375em;
      }
    }
    &__hidden{
      margin-top: -.75em;
      padding: .625em 1.125em 1em;
    }
    &__text{
      font-size: .875em;
      max-width: 80%;
    }
  }
}


