.consult{
  padding: 12em 0 9.75em;
  &__inner{
    padding: 2.67em 3em 4.44em;
    border-radius: 0.75em;
    background: linear-gradient(180deg, rgba(69, 145, 240, 0.15) 0%, rgba(69, 145, 240, 0.00) 92.31%);
    max-width: 64.8125em;
    position: relative;
  }
  &__top{
    @include grid100gap(1em);
  }
  &__title{
    font-size: 2.125em;
    line-height: 165%;
    font-weight: 800;
  }
  &__desc{
    font-size: 1.25em;
    line-height: 110%;
  }
  &__img{
    position: absolute;
    right: -12em;
    top: -12em;
    width: 29.625em;
    z-index: 3;
  }
  &__grad{
    position: absolute;
    right: .375em;
    top: -10.36em;
    width: 20.3em;
  }
  &__grad2{
    position: absolute;
    right: -14.44em;
    top: 2.53em;
    width: 26.03em;
  }
}

.consult-form{
  margin-top: 2em;
  position: relative;
  padding-right: 14em;
  max-width: 39.25em;
  &__btn{
    position: absolute;
    top: 0;
    right: 0;
    width: 15.875em;
  }
}

.cities{
  padding: 8.5em 0 2.5em;
  &__inner{
    position: relative;
  }
  &__grid{
    margin-top: 2.5em;
    display: grid;
    grid-template-columns: calc(100% - 39em) 37.75em;
    gap: 1.25em;
  }
  &__arrow{
    position: absolute;
    top: -8.5em;
    right: 4.375em;
    transform: rotate(-109.462deg);
    width: 6.06844em;
  }
  &__col{
    position: relative;
    min-height: 27em;
  }
  &__img{
    position: absolute;
    top: -1.625em;
    right: 0;
    width: 37.75em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__item{
    height: 6.25em;
    @include center;
    border-radius: 0.5em;
    background: rgba(35, 35, 35, 0.01);
    box-shadow: 2px 1px 4px 0 rgba(133, 133, 133, 0.25) inset, 7px 6px 4px 0 rgba(0, 0, 0, 0.25), 0 7px 13px 0 rgba(69, 69, 69, 0.05);
    font-family: $font2;
    font-weight: 700;
    color: $primary;
    span{
      font-size: 1.125em;
    }
  }
  &__nav{
    margin-top: 2em;
    display: flex;
    justify-content: center;
  }
  &__btn{
    min-width: 15.875em;
  }
}


@media screen and (max-width: 992px){
  .consult{
    padding: 1.25em 0 2.5em;
    &__inner{
      padding: 1.375em 1.5em 1.5em;
      background: linear-gradient(180deg, rgba(69, 145, 240, 0.15) 0%, rgba(69, 145, 240, 0.00) 92.31%);
    }
    &__top{
      gap: .625em;
    }
    &__title{
      font-size: 1.5em;
      line-height: 121%;
    }
    &__desc{
      font-size: .875em;
      line-height: 157%;
    }
    &__img{
      right: .125em;
      top: 3.375em;
      width: 9.25em;
      z-index: 2;
    }
    &__grad,
    &__grad2{
      display: none;
    }
  }

  .consult-form{
    z-index: 3;
    margin-top: 5.5em;
  }

  .cities{
    padding: 1.25em 0;
    &__grid{
      margin-top: 1.25em;
      grid-template-columns: 100%;
    }
    &__arrow,
    &__col{
      display: none;
    }
    &__items{
      @media screen and (min-width: 577px){
        grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
      }
    }
    &__item{
      height: 4.125em;
      span{
        font-size: 1em;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
    &__btn{
      width: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .consult{
    &__desc{
      max-width: 10em;
    }
  }

  .consult-form{
    padding: 0;
    &__btn{
      position: static;
      width: 100%;
      margin-top: 1.25em;
    }
  }
}
