.filter-slider{
  @include lastM(0 0 .75em);
  &__nums{
    display: flex;
    justify-content: space-between;
  }
  &__num{
    font-size: .9375em;
    line-height: 150%;
    font-weight: 700;
    color: $primary;
  }
  &__item{
    min-height: 1em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  &__float{
    margin-top: .31em;
    display: flex;
    &-inner{
      background: #F6F6F6;
      min-width: 5.6875em;
      padding: 1.125em 1em .66em;
      box-shadow: 2px 1px 3px 0 rgba(133, 133, 133, 0.15) inset;
      filter: drop-shadow(7px 6px 11.7px rgba(0, 0, 0, 0.10));
      //clip-path: polygon( 100% 86.17%,100% 24.468%,100% 24.468%,99.914% 22.397%,99.664% 20.433%,99.264% 18.601%,98.728% 16.929%,98.069% 15.441%,97.301% 14.165%,96.437% 13.127%,95.491% 12.353%,94.476% 11.869%,93.407% 11.702%,50% 11.702%,50% 11.702%,40.892% 11.48%,32.795% 10.86%,25.672% 9.912%,19.487% 8.706%,14.203% 7.314%,9.783% 5.804%,6.189% 4.248%,3.385% 2.715%,1.335% 1.276%,0% 0%,0% 86.17%,0% 86.17%,0.086% 88.241%,0.336% 90.205%,0.736% 92.037%,1.272% 93.71%,1.931% 95.197%,2.699% 96.473%,3.563% 97.511%,4.509% 98.285%,5.524% 98.769%,6.593% 98.936%,93.407% 98.936%,93.407% 98.936%,94.476% 98.769%,95.491% 98.285%,96.437% 97.511%,97.301% 96.473%,98.069% 95.197%,98.728% 93.71%,99.264% 92.037%,99.664% 90.205%,99.914% 88.241%,100% 86.17% );
    }
    &-text{
      font-size: .75em;
      line-height: 150%;
      font-weight: 700;
      color: $primary;
    }
  }
  .range-slider{
    width: 100%;
    background-color: #ccc;
    border: none;
    height: .375em;
    border-radius: 4.6875em;
    box-shadow: 0 0 27.1px 10px rgba(0, 0, 0, 0.03);
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $primary;
      width: 1em;
      height: 1em;
      top: -.3125em !important;
      right: -.3125em !important;
      border-radius: 50%;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__inputs{
    margin: .3125em 0 .6875em;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    gap: 1.06em;
    &-title{
      position: absolute;
      top: 50%;
      left: 1.25em;
      transform: translateY(-50%);
    }
  }
  .fg{
    position: relative;
    &:nth-child(1){
      .text14{
        transform: translateY(-.0125em);
      }
    }
    &:nth-child(2){
      .text14{
        transform: translateY(-.125em);
      }
    }
    input{
      height: 3.43em;
      color: $black;
      font-weight: 400;
      font-size: .875em;
      line-height: 140%;
      padding-left: 2.85em;
      border-radius: .214em;
    }
    span{
      position: absolute;
      top: 50%;
      left: 1em;
      transform: translateY(-50%);
      font-size: .9375em;
      line-height: 120%;
      color: #A7AAAE;
    }
  }
}
